""

import React, { useState, useEffect, useRef, useCallback, useMemo } from 'react';

interface PuzzlePiece {
  id: string;
  type: number;
  row: number;
  col: number;
  matched?: boolean;
  combo?: number;
}

interface TimelineEvent {
  id: string;
  position: number;
  type: 'damage' | 'heal' | 'buff' | 'debuff';
  value: number;
  target: 'player' | 'enemy';
  timestamp: number;
}

interface Ability {
  id: string;
  name: string;
  icon: string;
  cooldown: number;
  currentCooldown: number;
  description: string;
}

interface GameState {
  turn: number;
  playerHp: number;
  enemyHp: number;
  combo: number;
  score: number;
  isPaused: boolean;
}

interface ReplayFrame {
  gameState: GameState;
  grid: PuzzlePiece[][];
  timestamp: number;
}

const PuzzleBattleGame: React.FC = () => {
  const GRID_SIZE = 8;
  const PIECE_TYPES = 6;
  const [splitterPosition, setSplitterPosition] = useState(70);
  const [isDraggingSplitter, setIsDraggingSplitter] = useState(false);
  const [selectedPiece, setSelectedPiece] = useState<PuzzlePiece | null>(null);
  const [hoveredPiece, setHoveredPiece] = useState<PuzzlePiece | null>(null);
  const [grid, setGrid] = useState<PuzzlePiece[][]>([]);
  const [timelineEvents, setTimelineEvents] = useState<TimelineEvent[]>([]);
  const [abilities, setAbilities] = useState<Ability[]>([]);
  const [gameState, setGameState] = useState<GameState>({
    turn: 1,
    playerHp: 100,
    enemyHp: 100,
    combo: 0,
    score: 0,
    isPaused: false
  });
  const [replayFrames, setReplayFrames] = useState<ReplayFrame[]>([]);
  const [isReplaying, setIsReplaying] = useState(false);
  const [replayIndex, setReplayIndex] = useState(0);
  const [previewGrid, setPreviewGrid] = useState<PuzzlePiece[][] | null>(null);
  
  const containerRef = useRef<HTMLDivElement>(null);
  const svgRef = useRef<SVGSVGElement>(null);

  const colors = {
    primary: '#F2EB80',
    secondary: '#BF6A1F',
    dark: '#401A04',
    accent: '#8C441B',
    darkest: '#260801'
  };

  const pieceColors = [
    '#FF6B6B', '#4ECDC4', '#45B7D1', '#96CEB4', '#FECA57', '#DDA0DD'
  ];

  // Initialize grid
  useEffect(() => {
    initializeGrid();
    initializeAbilities();
  }, []);

  const initializeGrid = () => {
    const newGrid: PuzzlePiece[][] = [];
    for (let row = 0; row < GRID_SIZE; row++) {
      newGrid[row] = [];
      for (let col = 0; col < GRID_SIZE; col++) {
        newGrid[row][col] = {
          id: `${row}-${col}`,
          type: Math.floor(Math.random() * PIECE_TYPES),
          row,
          col
        };
      }
    }
    setGrid(newGrid);
  };

  const initializeAbilities = () => {
    setAbilities([
      { id: '1', name: 'Fire Blast', icon: 'üî•', cooldown: 3, currentCooldown: 0, description: 'Deal 15 damage' },
      { id: '2', name: 'Heal', icon: 'üíö', cooldown: 4, currentCooldown: 0, description: 'Restore 20 HP' },
      { id: '3', name: 'Shield', icon: 'üõ°Ô∏è', cooldown: 5, currentCooldown: 0, description: 'Block next attack' },
      { id: '4', name: 'Lightning', icon: '‚ö°', cooldown: 6, currentCooldown: 0, description: 'Deal 25 damage' }
    ]);
  };

  // Handle splitter drag
  const handleSplitterMouseDown = (e: React.MouseEvent) => {
    e.preventDefault();
    setIsDraggingSplitter(true);
  };

  useEffect(() => {
    const handleMouseMove = (e: MouseEvent) => {
      if (!isDraggingSplitter || !containerRef.current) return;
      
      const containerRect = containerRef.current.getBoundingClientRect();
      const relativeX = e.clientX - containerRect.left;
      const percentage = (relativeX / containerRect.width) * 100;
      setSplitterPosition(Math.max(50, Math.min(80, percentage)));
    };

    const handleMouseUp = () => {
      setIsDraggingSplitter(false);
    };

    if (isDraggingSplitter) {
      document.addEventListener('mousemove', handleMouseMove);
      document.addEventListener('mouseup', handleMouseUp);
    }

    return () => {
      document.removeEventListener('mousemove', handleMouseMove);
      document.removeEventListener('mouseup', handleMouseUp);
    };
  }, [isDraggingSplitter]);

  // Check for matches
  const checkMatches = useCallback((currentGrid: PuzzlePiece[][]): PuzzlePiece[][] => {
    const newGrid = currentGrid.map(row => row.map(piece => ({ ...piece, matched: false })));
    let foundMatch = false;
    let comboCount = 0;

    // Check horizontal matches
    for (let row = 0; row < GRID_SIZE; row++) {
      for (let col = 0; col <= GRID_SIZE - 3; col++) {
        const type = newGrid[row][col].type;
        if (type !== -1 && 
            newGrid[row][col + 1].type === type && 
            newGrid[row][col + 2].type === type) {
          for (let i = 0; i < 3; i++) {
            newGrid[row][col + i].matched = true;
            newGrid[row][col + i].combo = comboCount;
          }
          foundMatch = true;
          comboCount++;
        }
      }
    }

    // Check vertical matches
    for (let col = 0; col < GRID_SIZE; col++) {
      for (let row = 0; row <= GRID_SIZE - 3; row++) {
        const type = newGrid[row][col].type;
        if (type !== -1 && 
            newGrid[row + 1][col].type === type && 
            newGrid[row + 2][col].type === type) {
          for (let i = 0; i < 3; i++) {
            newGrid[row + i][col].matched = true;
            newGrid[row + i][col].combo = comboCount;
          }
          foundMatch = true;
          comboCount++;
        }
      }
    }

    if (foundMatch) {
      // Add timeline events for matches
      const damage = comboCount * 5;
      const newEvent: TimelineEvent = {
        id: Date.now().toString(),
        position: Math.random() * 100,
        type: 'damage',
        value: damage,
        target: 'enemy',
        timestamp: Date.now()
      };
      setTimelineEvents(prev => [...prev, newEvent]);
      
      // Update game state
      setGameState(prev => ({
        ...prev,
        enemyHp: Math.max(0, prev.enemyHp - damage),
        combo: prev.combo + comboCount,
        score: prev.score + (damage * 10)
      }));
    }

    return newGrid;
  }, []);

  // Handle piece swap
  const swapPieces = (piece1: PuzzlePiece, piece2: PuzzlePiece) => {
    if (Math.abs(piece1.row - piece2.row) + Math.abs(piece1.col - piece2.col) !== 1) {
      return;
    }

    setGrid(prevGrid => {
      const newGrid = prevGrid.map(row => [...row]);
      const temp = newGrid[piece1.row][piece1.col];
      newGrid[piece1.row][piece1.col] = newGrid[piece2.row][piece2.col];
      newGrid[piece2.row][piece2.col] = temp;
      
      // Update positions
      newGrid[piece1.row][piece1.col].row = piece1.row;
      newGrid[piece1.row][piece1.col].col = piece1.col;
      newGrid[piece2.row][piece2.col].row = piece2.row;
      newGrid[piece2.row][piece2.col].col = piece2.col;

      // Check for matches after swap
      const matchedGrid = checkMatches(newGrid);
      
      // Save replay frame
      setReplayFrames(prev => [...prev, {
        gameState: { ...gameState },
        grid: matchedGrid,
        timestamp: Date.now()
      }]);

      return matchedGrid;
    });

    setSelectedPiece(null);
  };

  // Handle piece click
  const handlePieceClick = (piece: PuzzlePiece) => {
    if (selectedPiece) {
      swapPieces(selectedPiece, piece);
    } else {
      setSelectedPiece(piece);
    }
  };

  // Handle piece hover for preview
  const handlePieceHover = (piece: PuzzlePiece) => {
    setHoveredPiece(piece);
    
    if (selectedPiece && piece !== selectedPiece) {
      // Create preview grid
      const newGrid = grid.map(row => row.map(p => ({ ...p })));
      if (Math.abs(selectedPiece.row - piece.row) + Math.abs(selectedPiece.col - piece.col) === 1) {
        const temp = newGrid[selectedPiece.row][selectedPiece.col];
        newGrid[selectedPiece.row][selectedPiece.col] = newGrid[piece.row][piece.col];
        newGrid[piece.row][piece.col] = temp;
        
        const previewWithMatches = checkMatches(newGrid);
        setPreviewGrid(previewWithMatches);
      }
    }
  };

  // Handle ability use
  const useAbility = (ability: Ability) => {
    if (ability.currentCooldown > 0) return;

    let eventType: TimelineEvent['type'] = 'damage';
    let value = 0;
    let target: 'player' | 'enemy' = 'enemy';

    switch (ability.name) {
      case 'Fire Blast':
        eventType = 'damage';
        value = 15;
        target = 'enemy';
        break;
      case 'Heal':
        eventType = 'heal';
        value = 20;
        target = 'player';
        break;
      case 'Shield':
        eventType = 'buff';
        value = 1;
        target = 'player';
        break;
      case 'Lightning':
        eventType = 'damage';
        value = 25;
        target = 'enemy';
        break;
    }

    const newEvent: TimelineEvent = {
      id: Date.now().toString(),
      position: Math.random() * 100,
      type: eventType,
      value,
      target,
      timestamp: Date.now()
    };

    setTimelineEvents(prev => [...prev, newEvent]);
    
    // Update ability cooldown
    setAbilities(prev => prev.map(a => 
      a.id === ability.id 
        ? { ...a, currentCooldown: a.cooldown }
        : a
    ));

    // Apply ability effect
    setGameState(prev => {
      let newState = { ...prev };
      if (eventType === 'damage' && target === 'enemy') {
        newState.enemyHp = Math.max(0, prev.enemyHp - value);
      } else if (eventType === 'heal' && target === 'player') {
        newState.playerHp = Math.min(100, prev.playerHp + value);
      }
      return newState;
    });
  };

  // Timeline path generator
  const generateTimelinePath = (isPlayer: boolean) => {
    const y = isPlayer ? 30 : 70;
    return `M 10,${y} Q 200,${y - 20} 400,${y} T 800,${y}`;
  };

  // Start replay
  const startReplay = () => {
    setIsReplaying(true);
    setReplayIndex(0);
  };

  // Replay animation
  useEffect(() => {
    if (!isReplaying || replayIndex >= replayFrames.length) {
      setIsReplaying(false);
      return;
    }

    const timer = setTimeout(() => {
      const frame = replayFrames[replayIndex];
      setGrid(frame.grid);
      setGameState(frame.gameState);
      setReplayIndex(prev => prev + 1);
    }, 500);

    return () => clearTimeout(timer);
  }, [isReplaying, replayIndex, replayFrames]);

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 to-gray-800 flex items-center justify-center p-4">
      <div 
        ref={containerRef}
        className="w-full max-w-[1600px] h-[900px] bg-gray-900 rounded-2xl shadow-2xl overflow-hidden flex flex-col"
        style={{ backgroundColor: colors.darkest }}
      >
        {/* Header */}
        <div className="h-20 bg-gradient-to-r from-gray-800 to-gray-900 flex items-center justify-between px-8 border-b border-gray-700">
          <div className="flex items-center space-x-6">
            <h1 className="text-3xl font-bold text-white">Puzzle Battle</h1>
            <div className="flex items-center space-x-4">
              <span className="text-lg text-gray-300">Turn: {gameState.turn}</span>
              <span className="text-lg text-gray-300">Score: {gameState.score}</span>
              <span className="text-lg text-yellow-400">Combo: x{gameState.combo}</span>
            </div>
          </div>
          <div className="flex space-x-4">
            <button
              onClick={startReplay}
              className="px-6 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded-lg transition-colors"
              disabled={replayFrames.length === 0}
            >
              Replay ({replayFrames.length})
            </button>
            <button
              onClick={initializeGrid}
              className="px-6 py-2 bg-green-600 hover:bg-green-700 text-white rounded-lg transition-colors"
            >
              New Game
            </button>
          </div>
        </div>

        {/* Timeline Area */}
        <div className="h-32 bg-gray-800 border-b border-gray-700 relative">
          <svg ref={svgRef} className="w-full h-full">
            {/* Player Timeline */}
            <path
              d={generateTimelinePath(true)}
              fill="none"
              stroke={colors.primary}
              strokeWidth="3"
              opacity="0.3"
            />
            <text x="10" y="20" fill={colors.primary} className="text-sm font-semibold">
              Player HP: {gameState.playerHp}
            </text>
            
            {/* Enemy Timeline */}
            <path
              d={generateTimelinePath(false)}
              fill="none"
              stroke={colors.secondary}
              strokeWidth="3"
              opacity="0.3"
            />
            <text x="10" y="90" fill={colors.secondary} className="text-sm font-semibold">
              Enemy HP: {gameState.enemyHp}
            </text>

            {/* Timeline Events */}
            {timelineEvents.slice(-10).map((event, index) => (
              <g key={event.id} className="animate-pulse">
                <circle
                  cx={`${event.position}%`}
                  cy={event.target === 'player' ? 30 : 70}
                  r="8"
                  fill={event.type === 'damage' ? '#FF6B6B' : event.type === 'heal' ? '#4ECDC4' : '#FECA57'}
                  className="animate-bounce"
                />
                <text
                  x={`${event.position}%`}
                  y={event.target === 'player' ? 25 : 65}
                  textAnchor="middle"
                  fill="white"
                  className="text-xs font-bold"
                >
                  {event.value}
                </text>
              </g>
            ))}
          </svg>
        </div>

        {/* Main Game Area */}
        <div className="flex-1 flex relative">
          {/* Puzzle Grid */}
          <div 
            className="bg-gray-800 p-8 flex items-center justify-center"
            style={{ width: `${splitterPosition}%` }}
          >
            <div className="grid grid-cols-8 gap-1 p-4 bg-gray-900 rounded-xl shadow-inner">
              {grid.map((row, rowIndex) => (
                row.map((piece, colIndex) => {
                  const isSelected = selectedPiece?.id === piece.id;
                  const isHovered = hoveredPiece?.id === piece.id;
                  const isPreviewMatch = previewGrid?.[rowIndex]?.[colIndex]?.matched;
                  
                  return (
                    <div
                      key={piece.id}
                      onClick={() => handlePieceClick(piece)}
                      onMouseEnter={() => handlePieceHover(piece)}
                      onMouseLeave={() => {
                        setHoveredPiece(null);
                        setPreviewGrid(null);
                      }}
                      className={`
                        w-16 h-16 rounded-lg cursor-pointer transition-all duration-200
                        ${isSelected ? 'ring-4 ring-yellow-400 scale-110' : ''}
                        ${isHovered && !isSelected ? 'scale-105' : ''}
                        ${piece.matched ? 'animate-pulse ring-2 ring-white' : ''}
                        ${isPreviewMatch ? 'ring-2 ring-green-400 opacity-80' : ''}
                      `}
                      style={{
                        backgroundColor: piece.matched 
                          ? colors.primary 
                          : pieceColors[piece.type],
                        boxShadow: '0 4px 6px rgba(0, 0, 0, 0.3)'
                      }}
                    >
                      <div className="w-full h-full flex items-center justify-center text-2xl font-bold text-white">
                        {['üíé', 'üîÆ', '‚≠ê', 'üåü', 'üí´', '‚ú®'][piece.type]}
                      </div>
                    </div>
                  );
                })
              ))}
            </div>
          </div>

          {/* Splitter */}
          <div
            className="w-2 bg-gray-700 cursor-col-resize hover:bg-gray-600 transition-colors flex items-center justify-center"
            onMouseDown={handleSplitterMouseDown}
          >
            <div className="w-1 h-16 bg-gray-500 rounded-full" />
          </div>

          {/* Ability Bar */}
          <div 
            className="bg-gray-800 p-8 overflow-y-auto"
            style={{ width: `${100 - splitterPosition}%` }}
          >
            <h2 className="text-2xl font-bold text-white mb-6">Abilities</h2>
            <div className="space-y-4">
              {abilities.map(ability => (
                <div
                  key={ability.id}
                  onClick={() => useAbility(ability)}
                  className={`
                    p-4 bg-gray-700 rounded-lg cursor-pointer transition-all duration-200
                    ${ability.currentCooldown > 0 
                      ? 'opacity-50 cursor-not-allowed' 
                      : 'hover:bg-gray-600 hover:scale-105'
                    }
                  `}
                  style={{
                    boxShadow: '0 4px 6px rgba(0, 0, 0, 0.3)'
                  }}
                >
                  <div className="flex items-center justify-between">
                    <div className="flex items-center space-x-3">
                      <span className="text-3xl">{ability.icon}</span>
                      <div>
                        <h3 className="text-lg font-semibold text-white">{ability.name}</h3>
                        <p className="text-sm text-gray-400">{ability.description}</p>
                      </div>
                    </div>
                    {ability.currentCooldown > 0 && (
                      <div className="text-2xl font-bold text-red-400">
                        {ability.currentCooldown}
                      </div>
                    )}
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>

        {/* Status Bar */}
        <div className="h-16 bg-gray-800 border-t border-gray-700 flex items-center justify-between px-8">
          <div className="flex items-center space-x-6">
            <div className="flex items-center space-x-2">
              <div className="w-3 h-3 rounded-full bg-green-500 animate-pulse" />
              <span className="text-sm text-gray-300">
                {isReplaying ? 'Replaying...' : 'Ready'}
              </span>
            </div>
          </div>
          <div className="text-sm text-gray-400">
            {new Date().toLocaleTimeString()}
          </div>
        </div>
      </div>
    </div>
  );
};

export default PuzzleBattleGame;